import React, {memo, useMemo, useState} from "react";
import ReactEcharts from "echarts-for-react";
import {EChartsOption} from "echarts-for-react/src/types";
import * as echarts from 'echarts';
import classNames from "classnames";
import {ClassNameElement} from "../types";
import {ChartDescPrev} from "../ChartDescPrev";

const chartData = [
  {
    value: 1839.8,
    prev: 1921.3,
  },
  {
    value: 1807.0,
    prev: 1949.5,
  },
  {
    value: 1646.8,
    prev: 1920.2,
  },
  {
    value: 1570.6,
    prev: 1880.7,
  },
  {
    value: 1444.4,
    prev: 1732.8,
  },
  {
    value: 1361.6,
    prev: 1574.6,
  },
  {
    value: 1353.2,
    prev: 1527.1,
  },
  {
    value: 1485.3,
    prev: 1541.0,
  },
  {
    value: 1608.4,
    prev: 1617.6,
  },
  {
    value: 1776.7,
    prev: 1736.3,
  },
  {
    value: 1840.0,
    prev: 1873.7,
  },
  {
    value: 1884.7,
    prev: 1900.7,
  },
];

interface LineChartProps extends ClassNameElement {
}

const LineChart = memo(({className}: LineChartProps) => {
  const [active, setActive] = useState(chartData.length - 1);

  const prevDataChart = useMemo(() => (
    chartData.map(item => {
      return ({value: item.prev});
    })), [chartData]);


  return (
    <div className={classNames('lineChart', className)}>
      <h4 className="lineChart__title">Среднесуточная добыча газа</h4>
      <ReactEcharts
        className='lineChart__chart'
        style={{
          height: '500px',
        }}
        option={{
          xAxis: {
            type: 'category',
            data: ['янв', 'фев', 'мар', 'апр', 'май', 'июн', 'июл', 'авг', 'сен', 'окт', 'ноя', 'дек'],
            axisTick: {
              show: false,
            },
          },
          animationDuration: 0,
          grid: {
            left: 40,
          },
          height: 350,
          yAxis: {
            type: 'value',
            min: 1250,
            max: 2250,
            interval: 250,
          },
          series: [
            {
              data: prevDataChart,
              type: 'line',
              itemStyle: {
                color: '#71c5bc',
              },
              lineStyle: {
                width: 0
              },
              selectionMode: false,
              showSymbol: false,
              areaStyle: {
                opacity: 0.8,
                color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
                  {
                    offset: 0,
                    color: '#71c5bc'
                  },
                  {
                    offset: 1,
                    color: 'white'
                  }
                ])
              },
            },
            {
              data: chartData,
              animationDuration: 0,
              type: 'line',
              itemStyle: {
                color: '#1B3A98',
              },
              symbol: 'circle',
              symbolSize: (value: any, params: any) => {
                if (chartData[active].value === value) {
                  return 10;
                } else {
                  return 5;
                }
              },
              label: {
                show: true,
                position: 'top',
                fontWeight: 700,
                fontSize: 15,
                color: '#000',
                formatter: (config: any) => {
                  return config.dataIndex === active ? config.data.value : '';
                },
              },
            }
          ]
        }}
        onEvents={{
          'mouseover': (e: EChartsOption) => {
            setActive(e?.dataIndex);
          }
        }}
      />
      <ChartDescPrev
        chartData={chartData}
        active={active}
        title='Изменение к отчетному году'
        measurement='млн куб. м/сут.'
      />
    </div>
  );
});

export {LineChart};