import React, {memo} from "react";
import {COLOR, GAZ_PAGE_CONFIG} from "../../constants";

interface BarDescFieldProps {
  active: number
}

const BarDescField = memo(({active}: BarDescFieldProps) => {
  return (
    <div className="barDescField">
      <p className="barDescField__amount" style={{
        color: COLOR[active],
      }
      }>{GAZ_PAGE_CONFIG[active].amount}</p>
      <p className="barDescField__desc">{GAZ_PAGE_CONFIG[active].desc}</p>
      <p className="barDescField__desc">Изменение к аналогичному периоду прошлого года</p>
      <p className="barDescField__shift">{GAZ_PAGE_CONFIG[active].shift}</p>
      <p className="barDescField__desc">П.П.</p>
    </div>
  );
});

export {BarDescField};