import classNames from 'classnames';
import React, {useEffect, useState} from 'react';
import {NavLink, useLocation} from "react-router-dom";
import {IconLogo} from '../../icons/logo/logo';
import { Subheader } from './components';
import {PATHS, ROUTE} from './constants';

interface HeaderProps {
  sectionScroll: {
    [key: string]: number,
  },
}

const Header = ({sectionScroll}: HeaderProps) => {
  const [isOpened, setIsOpened] = useState(false);

  const {pathname} = useLocation();

  useEffect(() => {
    setIsOpened(false);
  }, [pathname])

  return (
    <header className='header'>
      <div className="header__content">
        <NavLink className='header__logo-wrapper' exact to={PATHS.HOME}>
          <IconLogo className='header__logo'/>
        </NavLink>
        <div className="header__nav-box">
          <span className='header__nav-desc'>Промышленность:</span>
          <nav className='header__nav'>
            {ROUTE.map(route => (
              <NavLink key={route.path} className='header__nav-item' to={route.path}>
                <p className='header__nav-value'>{route.name}</p>
                {pathname === route.path && <button className={classNames({'opened': isOpened}, 'header__toggle')} onClick={() => setIsOpened(!isOpened)}/>}
              </NavLink>
            ))}
          </nav>
        </div>
      </div>
      <Subheader
        className={classNames({'visible': (pathname !== PATHS.HOME) && isOpened})}
        setIsOpened={setIsOpened}
        pathname={pathname}
        sectionScroll={sectionScroll}
      />
    </header>
  )
}

export {Header};