import React, {memo, useEffect, useMemo, useRef, useState} from "react";
import ReactEcharts from "echarts-for-react";
import {EChartsOption} from "echarts-for-react/src/types";
import { ChartDesc } from "../ChartDesc";
import classNames from "classnames";
import {ChartDataProps, ClassNameElement} from "../types";

interface BarVertChartProps extends ClassNameElement {
  chartData: ChartDataProps,
  interval?: number,
  withButton?: boolean,
}

const BarVertChart = memo(({className, chartData, interval = 160, withButton = false}: BarVertChartProps) => {
  const [active, setActive] = useState(0);
  const [newData, setNewData] = useState<ChartDataProps>([]);

  useEffect(() => {
    setNewData(chartData.map((item, i) => {
      if (i === active) {
        item.itemStyle = {
          color: '#1B3A98',
        }
      } else {
        delete item.itemStyle;
      }

      return item;
    }));
  }, [active, chartData]);

  const chartConfig = {
    title: {
      subtext: 'млрд куб. м'
    },
    height: 200,
    grid: {
      left: 40,
    },
    xAxis: {
      type: 'category',
      data: ['2011', '2012', '2013', '2014', '2015', '2016', '2017', '2018', '2019', '2020'],
      axisTick: {
        show: false,
      },
    },
    yAxis: {
      axisLine: {
        show: false,
      },
      axisLabel: {
        color: '#000',
      },
      minInterval: interval,
      axisTick: {
        show: false,
      },
    },
    animationDuration: 0,
    series: [
      {
        data: newData,
        type: 'bar',
        itemStyle: {
          color: '#71c5bc',
        },
        barMaxWidth: 30,
        label: {
          show: true,
          position: 'top',
          fontWeight: 700,
          fontSize: 15,
          color: '#000',
          formatter: (config: any) => {
            return config.dataIndex === active ? config.data.value : '';
          },
        },
      },
    ],
  };

  return (
    <section className={classNames("barVertChart", className)}>
      <div className="barVertChart__chart-box">
        <ReactEcharts
          className='barVertChart__chart barVertChart__chart--aux'
          option={chartConfig}
          onEvents={{
            mouseover: (e: EChartsOption) => {
              setActive(e?.dataIndex);
            }
          }}
        />
      </div>
      <ChartDesc
        className='barVertChart__desc'
        chartData={chartData}
        active={active}
        measurement='млрд куб.'
        withButton={withButton}
      />
    </section>
  );
});

export {BarVertChart};