const ChartDesc = memo(({className, chartData, measurement, active, withButton = false}: ChartDescProps) => {
  const [isModalOpen, setIsModalOpen] = useState<boolean>(false);

  const isLastPreLast = active >= chartData.length - 2;

  const showValueChange = () => {
    if (isLastPreLast) {
      return Math.round((chartData[chartData.length - 1].value - chartData[chartData.length - 2].value) * 10) / 10;
    }

    return Math.round((chartData[chartData.length - 1].value - chartData[active].value) * 10) / 10;
  };

  const showShiftChange = () => {
    return Math.round(showValueChange() / (chartData[chartData.length - 1].value / 100) * 10) / 10;
  };

  return (
    <div className={classNames('chartDesc', className)}>
      <p className="chartDesc__title">Изменение <br/> к отчетному году</p>
      <b className="chartDesc__shift">{showShiftChange()}</b>
      <b className="chartDesc__value">{showValueChange()}</b>
      <span className="chartDesc__measurement">{measurement}</span>
      {withButton && <Button
          className='chartDesc__button'
          onClick={() => {
            setIsModalOpen(true)
          }}>Подробнее</Button>}
      {isModalOpen && <Modal
          className="chartDesc__modal"
          onClose={() => {
            setIsModalOpen(false);
          }}
      >
          <div className="chartDesc__tableRow chartDesc__tableRow--title">
              <span className="chartDesc__cell chartDesc__cell--wide">2020</span>
              <span className="chartDesc__cell">±% к2019</span>
          </div>
          <ul className="chartDesc__tableContent">
            {tableData.map(item => (
              <li
                key={item.name}
                className="chartDesc__tableRow">
                <span className="chartDesc__cellName">{item.name}</span>
                <span className="chartDesc__cellValue">{item.value}</span>
                {item.shift ? <span
                    className={classNames("chartDesc__cellShift", {'plus': item.shift[0] === '+'})}>{item.shift}</span> :
                  <span className='chartDesc__cellShift chartDesc__cellShift--none'>–</span>}
              </li>
            ))}
          </ul>
          <div className="chartDesc__tableRow chartDesc__tableRow--footer">
              <span className="chartDesc__cellName">Всего</span>
              <span className="chartDesc__cellValue">77 716,4</span>
              <span className="chartDesc__cellShift">–4,1</span>
          </div>
      </Modal>}
    </div>
  )
});

export {ChartDesc};