import React, {memo, useEffect, useState} from "react";
import {BarChart} from "../../../../components/BarChart";
import { ChartDescPrev } from "../../../../components/ChartDescPrev";
import {ChartDataProps, PrevChartItemProps} from "../../../../components/types";

interface BarChartMiningProps {
  chartData: PrevChartItemProps[],
}

const BarChartMining = memo(({chartData}: BarChartMiningProps) => {
  const [active, setActive] = useState(0);

  const [newData, setNewData] = useState<ChartDataProps>([]);

  useEffect(() => {
    setNewData(chartData.map((item, i) => {
      if (i === active) {
        item.itemStyle = {
          color: '#1B3A98',
        }
      } else {
        delete item.itemStyle;
      }

      return item;
    }));
  }, [active, chartData]);

  return (
    <div className="barChartMining">
      <BarChart
        setActive={setActive}
        height='600px'
        option={{
          title: {
            subtext: 'млрд куб. м',
          },
          height: 550,
          animationDuration: 0,
          grid: {
            left: 150,
          },
          xAxis: {
            show: false,
          },
          yAxis: {
            type: 'category',
            data: ['Газпром', 'НОВАТЭК', 'Роснефть', 'Газпром нефть', 'ЛУКОЙЛ', 'Сургутнефтегаз', 'РуссНефть', 'Татнефть', 'ННК', 'СЛАВНЕФТЬ', 'Башнефть'],
            inverse: true,
            axisLine: {
              show: false,
            },
            axisLabel: {
              margin: 10,
              fontWeight: 700,
              fontSize: 15,
              color: '#000',
            },
            axisTick: {
              show: false,
            },
          },
          series: [
            {
              data: newData,
              label: {
                show: true,
                position: 'right',
                fontWeight: 700,
                fontSize: 15,
                color: '#000',
                formatter: '{c}',
              },
              type: 'bar',
              itemStyle: {
                color: '#0D988C',
              },
            }
          ],
        }}
      />
      <ChartDescPrev
        chartData={chartData}
        active={active}
        title='Изменение к аналогичному периоду прошлого года'
        measurement='млрд куб. м'
      />
    </div>
  )
});

export {BarChartMining};