import React, {memo, PropsWithChildren} from "react";
import classNames from "classnames";
import {ClassNameElement} from "../../types";

interface TabsProps extends ClassNameElement {
  tabs: TabProps[],
  active: number,
  setActive: (value: number | ActiveWithPrev) => void,
  withPrev?: boolean,
  mode?: 'link';
}

interface TabProps {
  name: string,
}

type ActiveWithPrev = (prev: number) => number;

const Tabs = memo(({tabs, active, setActive, className, withPrev = false, children, mode}: PropsWithChildren<TabsProps>) => {

  return (
    <>
      <ul className={classNames("tabs", className, mode && mode)}>
        {tabs.map((tab, i) => (
          <li
            key={`${tab.name}${i}`}
          >
            <button
              onClick={() => {
                if (withPrev) {
                  setActive(prev => prev === 0 ? 1 : 0)
                } else {
                  setActive(i);
                }
              }}
              className={classNames('tabs__item', {'active': active === i})}
            >
              {tab.name}
            </button>
          </li>
        ))}
      </ul>
      {children}
    </>
  );
});

export {Tabs};
