import React, {memo} from "react";
import classNames from "classnames";
import {ChildrenElement, ClassNameElement} from "../../types";

interface ButtonProps extends ClassNameElement {
  children: ChildrenElement | string,
  onClick: () => void,
}

const Button = memo(({className, children, onClick}: ButtonProps) => {
  return (
    <button
      className={classNames('button', className)}
      onClick={onClick}
    >
      {children}
    </button>
  )
});

export {Button};