import React, {memo} from "react";
import { ClassNameElement } from "../../types";
import classNames from "classnames";

interface TitleProps extends ClassNameElement {
  value: string;
}

const Heading = memo(({value, className}: TitleProps) => {
  return (
    <h4 className={classNames("heading", className)}>{value}</h4>
  );
});

export {Heading};
