import React, {memo} from "react";
import {PrevChartItemProps} from "../types";

interface BarDescMiningProps {
  chartData: PrevChartItemProps[],
  active: number,
  title: string,
  measurement: string,
}

const ChartDescPrev = memo(({chartData, active, title, measurement}: BarDescMiningProps) => {
  const showValueChange = () => {
    return Math.round((chartData[active].value - chartData[active].prev) * 10) / 10;
  };

  const showShiftChange = () => Math.round(showValueChange() / (chartData[active].value / 100) * 10) / 10;

  return (
    <div className="barDescMining">
      <p className="barDescMining__title">{title}</p>
      <b className="barDescMining__shift">{showShiftChange()}%</b>
      <b className="barDescMining__value">{showValueChange()}</b>
      <span className="barDescMining__measurement">{measurement}</span>
    </div>
  )
});

export {ChartDescPrev};